import axios from '@/store/axios'

const state = {
  test_results: []
}

const mutations = {
  SET_TEST_RESULTS: (state, testResults) => { state.test_results = testResults }
}

const actions = {
  getTestResults: ({commit}, id) => {
    axios.get('/api/v1/test_result')
      .then(({data}) => {
        commit('SET_TEST_RESULTS', data)
      })
  }
}

const getters = {
  testResults: state => state.test_results,

  testResult: state => id => {
    return state.test_results.find(testResult => testResult._id === id)
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}



