<template>
  <v-card flat app>
    <v-toolbar flat dark color="primary" app extended>
      <v-toolbar-side-icon @click.stop="$store.commit('navigationDrawerChange')"></v-toolbar-side-icon>
    </v-toolbar>

    <v-layout row pb-2>
      <v-flex :class="{ 'xs8': $vuetify.breakpoint.mdAndUp, 'offset-xs2': $vuetify.breakpoint.mdAndUp }">
        <v-card class="card--flex-toolbar" flat>
          <v-toolbar card prominent>
          <v-toolbar-title class="headline" :class="{ 'title--shrinked': $vuetify.breakpoint.mdAndDown}">Результаты тестов</v-toolbar-title>
          </v-toolbar>
          <v-divider></v-divider>
          <v-card-text>
            <v-data-table
              :headers="headers"
              :items="test_results"
              rows-per-page-text="Показывать на странице: "
              :rows-per-page-items="[10, 25, 50, { 'text': 'All', 'value': -1 }]"
            >
              <template slot="no-data"><v-alert :value="true" color="warning" icon="warning">Здесь ничего нет</v-alert></template>
              <template slot="items" slot-scope="props">
                <tr @click="$router.push({ name: 'TestResult', params: { id: props.item._id } })">
                  <td>{{ props.item.username }}</td>
                  <td>{{ props.item.test.title }}</td>
                  <td>{{ props.item.value }} из {{ props.item.answers.length }}</td>
                  <td>{{ new Date(props.item.test_ended).toLocaleString('ru') }}</td>
                </tr>
              </template>
            </v-data-table>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-card>
</template>
<script>
import TestResultMixin from './mixin'

export default {
  mixins: [TestResultMixin],

  data () {
    return {
      headers: [
        { value: 'username', text: 'Тестируемый' },
        { value: 'test.title', text: 'Тест' },
        { value: 'value', text: 'Баллов' },
        { value: 'test_ended', text: 'Сдано' }
      ]
    }
  },

  mounted () {
  }
}
</script>



