<template>
  <v-card flat app>
    <v-toolbar flat dark color="primary" app>
      <v-toolbar-side-icon @click.stop="$store.commit('navigationDrawerChange')"></v-toolbar-side-icon>
      <v-btn
        small
        color="primary"
        class="lighten-1"
        @click="$router.back()"
      >
        <v-icon>arrow_back</v-icon>Назад
      </v-btn>
    </v-toolbar>

    <v-ayout row pb-2>
      <v-flex :class="{ 'xs8': $vuetify.breakpoint.mdAndUp, 'offset-xs2': $vuetify.breakpoint.mdAndUp }">
        <v-card flat>
          <v-card-title class="headline" :class="{ 'title--shrinked': $vuetify.breakpoint.mdAndDown}">Результат теста: {{result.test.title}}</v-card-title>
          <v-card-text>
            <v-layout row pb-2>
              <v-flex>Выполнил: {{ result.username }}</v-flex>
              <v-flex>Дата сдачи: {{ new Date(result.test_ended).toLocaleString('ru') }}</v-flex>
              <v-flex>Время выполнения: {{ testTime }} {{ declOfNum(testTime, ['минута', 'минуты', 'минут']) }}</v-flex>
            </v-layout>
            <div>Правильно отвечено на {{result.value}} из {{result.answers.length}} {{declOfNum(result.answers.length, ['вопрос', 'вопроса', 'вопросов'])}}</div>
            <v-divider></v-divider>

            <v-card row flat
              v-for="(answer, index) in result.answers"
              :key="index"
            >
              <v-card-title v-html="result.test.questions[index].question" class="question"></v-card-title>
              <v-card-text>
                <ul>
                  <li
                    v-for="(item, index) in answer.answers"
                    :key="index"
                    :class="[item.correct && 'grey lighten-3', (item.correct && (item.userChoise === item.correct)) && 'success--text', item.userChoise && 'error--text']"
                    :style="item.userChoise && 'text-decoration:line-through;'"
                  >
                    {{item.title}}
                    <span v-if="item.correct"> - верный ответ</span>
                  </li>
                </ul>
              </v-card-text>
              <v-divider></v-divider>
            </v-card>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-ayout>
  </v-card>
</template>
<script>
import TestResultMixin from './mixin'

export default {
  mixins: [TestResultMixin],

  data () {
    return {
    }
  },

  computed: {
    result () { return this.testResult(this.$route.params['id']) },

    testTime () {
      let startTime = new Date(this.testResult(this.$route.params['id']).test_started)
      let endTime = new Date(this.testResult(this.$route.params['id']).test_ended)
      return Math.round((endTime - startTime) / 60000)
    }
  },

  mounted () {
  },

  methods: {
    declOfNum (number, titles) {
      let cases = [2, 0, 1, 1, 1, 2]
      // eslint-disable-next-line
      return titles[ (number%100>4 && number%100<20)? 2 : cases[(number%10<5)?number%10:5] ]
    }
  }
}
</script>



