import { mapGetters } from 'vuex'

var TestResultMixin = {
  computed: {
    ...mapGetters({
      test_results: 'testResult/testResults',
      testResult: 'testResult/testResult'
    })
  },

  mounted () {
    if (!this.test_results.length) this.updateTestResults()
  },

  methods: {
    updateTestResults () { this.$store.dispatch('testResult/getTestResults') }
  }
}

export default TestResultMixin



