<template>
  <v-container style="max-width: 400px;">
    <v-toolbar flat dark color="primary" app>
      <v-toolbar-side-icon @click.stop="$store.commit('navigationDrawerChange')"></v-toolbar-side-icon>
    </v-toolbar>

    <v-card v-if="!signUpVisible">
      <v-card-text>
        <v-form v-model="validLogin">
          <v-text-field label="Логин"
                        v-model="credentials.username"
                        prepend-icon="account_box"
                        :rules="rules"
                        required
                        color="light-blue lighten-1">
          </v-text-field>

          <v-text-field label="Пароль"
                        v-model="credentials.password"
                        prepend-icon="lock"
                        :rules="rules"
                        :append-icon="loginPasswordVisible ? 'visibility' : 'visibility_off'"
                        :append-icon-cb="() => (loginPasswordVisible = !loginPasswordVisible)"
                        :type="loginPasswordVisible ? 'text' : 'password'"
                        color="light-blue lighten-1"
                        required>
          </v-text-field>

          <v-layout row centered>
            <v-btn flat color="light-blue lighten-1" @click.native="signUpVisible = true">Создать аккаунт</v-btn>
            <v-spacer></v-spacer>
            <v-btn color="light-blue lighten-1 white--text" @click.native="submitAuthentication()">Войти</v-btn>
          </v-layout>
        </v-form>
      </v-card-text>
    </v-card>
    <v-card v-else>
      <v-form v-model="validSignUp" class="px-4">
        <v-text-field
          label="Логин"
          v-model="newUser.username"
          prepend-icon="account_box"
          :rules="rules"
          required
          color="light-blue lighten-1"
        ></v-text-field>
        <v-text-field
          label="Имя"
          v-model="newUser.first_name"
          :rules="rules"
          required
          color="light-blue lighten-1"
        ></v-text-field>
        <v-text-field
          label="Фамилия"
          v-model="newUser.last_name"
          :rules="rules"
          required
          color="light-blue lighten-1"
        ></v-text-field>
        <v-text-field label="Пароль"
          v-model="newUser.password"
          prepend-icon="lock"
          :rules="rules"
          :append-icon="signUpPasswordVisible ? 'visibility' : 'visibility_off'"
          :append-icon-cb="() => (signUpPasswordVisible = !signUpPasswordVisible)"
          :type="signUpPasswordVisible ? 'text' : 'password'"
          color="light-blue lighten-1"
          required
        ></v-text-field>

        <v-layout row>
          <v-btn flat color="light-blue lighten-1" @click.native="signUpVisible = false">Войти</v-btn>
          <v-spacer></v-spacer>
          <v-btn color="light-blue lighten-1 white--text" @click.native="submitSignUp()">Создать аккаунт</v-btn>
        </v-layout>
      </v-form>
    </v-card>
  </v-container>
</template>
<script>
import Authentication from '@/components/pages/Authentication'
export default {
  data () {
    return {
      validLogin: false,
      validSignUp: false,
      signUpVisible: false,
      loginPasswordVisible: false,
      signUpPasswordVisible: false,
      rules: [ (value) => !!value || 'Это поле обязательно' ],
      credentials: {
        username: '',
        password: ''
      },
      newUser: {
        username: '',
        password: '',
        first_name: '',
        last_name: ''
      }
    }
  },
  methods: {
    submitAuthentication () {
      Authentication.authenticate(this, this.credentials, { name: 'Home' })
    },

    submitSignUp () {
      Authentication.signup(this, this.newUser, { name: 'Home' })
    }
  }
}
</script>



